import { Component, Prop, Vue } from "vue-property-decorator";


import { FirestoreService } from '@/services/FirestoreService';
import { storage } from '@/firebaseInit';
import GiftCardItemInfo from "@/dto/GiftCardItemInfo";
import ProductImage from "@/dto/ProductImage";


@Component
export default class ProductImages extends Vue {

  loading = false;
  label = "Product Images";
  @Prop({ required: true })
  productImages!: ProductImage[];
  @Prop({ required: true })
  productId!: string;
  selectedImage: any = null;
  selectedHeaderImage = '';
  currentImage: ProductImage = new ProductImage();
  uploadHeaderBtnDisabled = true;
  uploadHeaderBtnLoading = false;
  saveItem = false;
  productItem = false;
  editId = "";
  deleteId = ""
  dialogDelete = false;

  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      if (this.currentImage.src !== '') {
        this.saveImage();
      } else {
        Vue.toasted.show('Please add header image ', { icon: 'face', type: 'error' });
      }
    }
  }
  resetForm() {
    this.currentImage = new ProductImage();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }
  async deleteItem() {
    try {
      const index = this.productImages.map((item) => item.id).indexOf(this.deleteId);
      const images = this.productImages.map((obj: any) => { return Object.assign({}, obj) });

      const snapshot = await FirestoreService.setOrMergeDocument('products', this.productId, {
        images: this.productImages
      });
      this.productImages.splice(index, 1);

      Vue.toasted.show('Item Deleted Successfully', { icon: 'face', type: 'success' });

    } catch (error) {
      Vue.toasted.show('Error deleting Gift card item', { icon: 'face', type: 'error' });
      console.log(error)
    } finally {
      this.dialogDelete = false;

    }

  }
  displayDeleteModal(item: any) {
    this.deleteId = item;
    this.dialogDelete = true;
  }
  closeDelete() {
    this.dialogDelete = false;
  }
  async saveImage() {
    try {
      this.saveItem = true;
      if (this.editId !== '') {
        const cItem: any = this.productImages.find(c => c.id === this.editId);
        Object.assign(cItem, this.currentImage);
        if (!cItem) {
          Vue.toasted.show('Error in updating item', { icon: 'face', type: 'error' });
          return;
        }
      } else {
        this.productImages.push(this.currentImage);
      }
      const images = this.productImages.map((obj: any) => { return Object.assign({}, obj) });

      const snapshot = await FirestoreService.setOrMergeDocument('products', this.productId, {
        images: images
      });
      Vue.toasted.show('Added Item Successfully', { icon: 'face', type: 'success' });
      this.selectedImage = null;
      this.selectedHeaderImage = '';
      this.productItem = false;
      this.resetForm();

    } catch (error) {
      Vue.toasted.show('Error fetching Gift Card Details', { icon: 'face', type: 'error' });
      console.log(error)
    }
    finally {
      this.saveItem = false;
    }
  }
  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedHeaderImage = URL.createObjectURL(this.selectedImage);
    this.uploadHeaderBtnDisabled = false;
  }

  addGiftCardItem(item: any) {
    console.log(item)
    if (item && item !== '') {
      this.editId = item;
      const cItem: any = this.productImages.find(c => c.id === item);
      this.currentImage = cItem;
      this.selectedHeaderImage = this.currentImage.src;
      this.productItem = true;
    } else {
      this.selectedHeaderImage = '';
      this.editId = '';
      this.productItem = true;
    }

  }
  cancelForm() {
    this.resetForm();
    this.productItem = false;
  }

  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }
  async uploadHeaderImage() {
    try {
      this.uploadHeaderBtnLoading = true;
      const fileExtension = this.getFileExtension(this.selectedImage);
      const metadata = {
        cacheControl: 'public,max-age=172800',
      };
      const resp = await storage.ref(`products/${this.productId}/images/${this.currentImage.id}.` + fileExtension).put(this.selectedImage, metadata);
      const url = await resp.ref.getDownloadURL();
      this.currentImage.src = url;
      Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.uploadHeaderBtnLoading = false;
    }


  }

}
