import { Component, Prop, Vue } from "vue-property-decorator";


import { PlatformDetails } from "@/dto/PlatformDetails";
import { PlatformInfo } from "@/dto/PlatformInfo";

@Component
export default class AddPlatform extends Vue {
  @Prop({ required: true })
  platformDetails!: PlatformDetails[];
  @Prop({ required: false, default: 0 })
  selectedIndex!: number;
  @Prop({ required: false, default: false })
  edit!: boolean;
  @Prop({ required: true })
  platformList!: PlatformInfo[];
  platform = new PlatformDetails();
  loading = false;
  label = "Add Platform";
  selectedPlatform: any = null;

  async mounted() {
    this.label = this.edit ? "Edit Platform" : "Add Platform";
    if (this.edit) {
      this.platform = this.platformDetails[this.selectedIndex];
      this.selectedPlatform = this.platform.platformInfo;
    } else {
      this.platform = new PlatformDetails();
    }
  }
  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.platform = new PlatformDetails();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }

  async processRequest() {
    try {
      if (this.edit)
        await this.updatePlatform();
      else {
        await this.addPlatform();

      }
    } catch (error) {

    }
  }
  platformChange() {
    this.platform.platformInfo = this.selectedPlatform;

  }

  async addPlatform() {

    try {
      this.platformDetails.push(this.platform);
      this.platformDetails.sort((a, b) => a.rank - b.rank);
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setAddPlatformPanel', false)

    }

  }

  cancelEdit() {
    this.$store.commit('setAddPlatformPanel', false)
  }
  async updatePlatform() {

    try {
      this.platformDetails[this.selectedIndex] = this.platform;
      this.platformDetails.sort((a, b) => a.rank - b.rank);
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setAddPlatformPanel', false)
    }
  }




}
