import { SellerInfo } from "./SellerInfo";
import { v4 as uuidv4 } from 'uuid';
import { ProductType } from "./ProductType";

export default class SellerDetails {
    id: string;
    sellerInfo: SellerInfo;
    price: number;
    rank: number;
    promoted: boolean;
    lowestPrice: boolean;
    productKeys: string[];
    productTypeOffered: ProductType;
    public constructor() {
        this.sellerInfo = new SellerInfo();
        this.price = 0;
        this.rank = 0;
        this.promoted = false;
        this.id = uuidv4();
        this.lowestPrice = false;
        this.productKeys = [];
        this.productTypeOffered = ProductType.DIGITAL;
    }
}

