import { Component, Prop, Vue, Watch } from "vue-property-decorator";

import { storage, auth } from '@/firebaseInit';
import { ProductInfo as Game } from '@/dto/ProductInfo';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import { UtilityService } from '@/services/UtilityService';

import { SellerInfo } from "@/dto/SellerInfo";

import AddSeller from "@/components/AddSeller/AddSeller.vue"
import AddPlatform from "@/components/AddPlatform/AddPlatform.vue"
import ProductImages from "@/components/ProductImages/ProductImages.vue"

import { PlatformInfo } from "@/dto/PlatformInfo";
import { ParentCategoryInfo } from "@/dto/ParentCategoryInfo";
import { SubCategoryInfo } from "@/dto/SubCategoryInfo";

import { VueEditor } from "vue2-editor";
import moment from "moment";
import { format, parseISO } from "date-fns";
@Component({
  components: {
    AddSeller, AddPlatform, ProductImages, VueEditor
  }
})
export default class Product extends Vue {
  @Prop({ required: false, default: function () { return new Game() } })
  productDetails!: Game;
  edit = false;
  //editor!: EditorJS;
  sellerList: SellerInfo[] = [];
  platformList: PlatformInfo[] = [];
  content = "Hello world"
  sellerEdit = false;
  uploadHeaderBtnDisabled = true;
  uploadHeaderBtnLoading = false;
  defaultGenre: string[] = ['Action', 'Adventure', 'FPS/TPS'];
  defaultPlatforms: string[] = ['PS4', 'PS5', 'PC', 'XBOX', 'MAC']
  defaultLanguages: string[] = [
    "English",
    "French",
    "German",
    "Italian",
    "Japanese",
    "Korean",
    "Polish",
    "Portuguese-Brazil",
    "Russian",
    "Spanish",
    "Traditional Chinese"
  ];
  selectedImage: any = null;
  selectedHeaderImage = '';
  holderLabel = '';
  loading = false;
  label = "Add Product";
  uploadRules = [
    (value: any) => !!value || 'logo is required',
    (value: any) =>
      !value ||
      value.size < 512000 ||
      'logo must be less than 500kb',
  ]
  sellerSearchLoading = false;
  editedIndex = 0;
  platformEdit = false;
  disableProduct = true;
  NameLading = false;
  //Parent-child categories
  parentSearchItems: ParentCategoryInfo[] = [];
  subCategoryList: SubCategoryInfo[] = [];

  selectedParentId = "";
  isParentLoading = false;
  parentSearch = "";
  isParentAvailable = false;
  //selectedMainCategory: any = undefined;
  subCategoriesLoading = false;
  preorderDateMenu = false;
  preOrderDate = format(parseISO(new Date().toISOString()), 'yyyy-MM-dd');
  get preOrderDateFormatted() {
    return this.preOrderDate ? moment(this.preOrderDate).format('DD/MM/YYYY') : '';
  }

  @Watch("parentSearch")
  async onPropertyChanged(value: string, oldValue: string) {
    try {
      if (value && value !== "" && value.length >= 3) {
        await this.loadParentCategories(value, "searchPhrase");
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });
    } finally {
      this.isParentLoading = false;
    }
  }
  async loadParentCategories(value: string, phrase: string) {
    try {
      this.isParentLoading = true;
      const snapshot = await FirestoreService.getdocsStartingWithKey(
        `parentCategories`,
        value.toLowerCase(),
        phrase,
        ParentCategoryInfo.converter
      );
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.parentSearchItems.push(doc.data());
        });
      } else {
        this.parentSearchItems = [];
      }
    } catch (error) {

    } finally {
      this.isParentLoading = false;

    }
  }
  async onProductChange(item: any) {
    if (item !== null) {
      this.isParentAvailable = true;
      await this.loadSubCategories();
    } else {
      this.isParentAvailable = false;
      this.subCategoryList = [];

    }
  }
  async loadSubCategories() {
    try {
      this.subCategoriesLoading = true;
      const snapshot = await FirestoreService.getDocuments(
        `parentCategories/${this.productDetails.mainCategory}/subCategories`,
        SubCategoryInfo.converter
      );
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.subCategoryList.push(doc.data());
        });
      } else {
        this.subCategoryList = [];
      }
    } catch (error) {
      Vue.toasted.show(error.message, { icon: "face", type: "error" });

    } finally {
      this.subCategoriesLoading = false;
    }
  }
  async mounted() {
    await this.getProductDetails();

  }
  private async getProductDetails() {
    console.log(this.$route.params.id)
    if (this.$route.params.id && this.$route.params.id !== 'new') {
      this.edit = true;
      try {
        const productData = await FirestoreService.getSingleDocument('products', this.$route.params.id as string, Game.converter);
        this.productDetails = productData.data();
        this.disableProduct = false;
      } catch (error) {
        Vue.toasted.show(error.message, { icon: "cross", type: "error" });
      }
      this.uploadRules = [
        (value: any) => !value ||
          value.size < 512000 ||
          'logo must be less than 500kb',
      ];
      if (this.productDetails.mainCategory) {
        this.isParentAvailable = true;
        await this.loadParentCategories(this.productDetails.mainCategory, "id");
        await this.loadSubCategories();

      }
      this.holderLabel = "gxc-description-editor";
      this.selectedHeaderImage = this.productDetails.headerImageUrl;
      if (this.productDetails.preOrder)
        this.preOrderDate = moment(this.productDetails.preOrderDate, 'DD/MM/YYYY').format('YYYY-MM-DD');

      await this.getSellers();
      await this.getPlatforms();
    }
    this.label = this.edit ? "Edit Product" : "Add Product";

  }

  @Watch('$route', { deep: true, })
  async onRouteChanged(value: boolean, oldValue: boolean) {
    await this.getProductDetails();
  }
  async validateForm() {
    if ((this.$refs.sellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.productDetails = new Game();
    this.selectedImage = null;
    this.selectedHeaderImage = '';
    (this.$refs.sellerForm as Vue & { reset: () => boolean }).reset();
  }
  previewImage(event: any) {
    this.selectedImage = event.target.files[0];
    this.selectedHeaderImage = URL.createObjectURL(this.selectedImage);
    this.uploadHeaderBtnDisabled = false;
  }
  clearImages() {
    this.productDetails.headerImageUrl = ""
  }
  async uploadHeaderImage() {
    try {
      this.uploadHeaderBtnLoading = true;

      if (this.productDetails.name) {
        this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
        const fileExtension = this.getFileExtension(this.selectedImage);
        const metadata = {
          cacheControl: 'public,max-age=172800',
        };

        const resp = await storage.ref(`products/${this.productDetails.id}/header.` + fileExtension).put(this.selectedImage, metadata);
        const url = await resp.ref.getDownloadURL();
        this.productDetails.headerImageUrl = url;

        Vue.toasted.show('Image Uploaded Successfully', { icon: 'face', type: 'success' });

      } else {
        Vue.toasted.show('specify product name first', { icon: 'cross', type: 'error' });
      }
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.uploadHeaderBtnLoading = false;
    }


  }
  async processRequest() {
    try {
      if (this.edit)
        await this.updateProduct();
      else {
        await this.addProduct();

      }
    } catch (error) {

    }
  }
  removeGenre(item: any) {
    this.productDetails.genre.splice(this.productDetails.genre.indexOf(item), 1)
    this.productDetails.genre = [...this.productDetails.genre]
  }
  removeLanguages(item: any) {
    this.productDetails.languages.splice(this.productDetails.languages.indexOf(item), 1)
    this.productDetails.languages = [...this.productDetails.languages]
  }
  removeWorksOn(item: any) {
    this.productDetails.worksOn.splice(this.productDetails.worksOn.indexOf(item), 1)
    this.productDetails.worksOn = [...this.productDetails.worksOn]
  }

  async addProduct() {
    Vue.toasted.show('Add product Method not in use', { icon: 'cross', type: 'error' });

    // if (this.productDetails.headerImageUrl !== "") {
    //   try {
    //     this.loading = true;
    //     this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
    //     this.productDetails.searchPhrase = this.productDetails.name.toLowerCase();
    //     // const resp = await storage.ref('product-images/' + this.productDetails.id + '.png').put(this.selectedImage);
    //     // const url = await resp.ref.getDownloadURL();
    //     // this.productDetails.logoUrl = url;
    //     // this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
    //     const dateNow = new Date();
    //     const auditfield: AuditFields = {
    //       createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
    //       createdOn: dateNow.getTime()
    //     }
    //     if (!this.checkSeller(this.productDetails)) {
    //       Vue.toasted.show('Product should have atleast one seller', { icon: 'cross', type: 'error' });
    //       return;
    //     }
    //     if (!this.checkProducts(this.productDetails)) {
    //       Vue.toasted.show('Product should have atleast 3 platform objects', { icon: 'cross', type: 'error' });
    //       return;
    //     }
    //     this.productDetails.auditTrail = auditfield;
    //     const editorResp = await this.editor.save();
    //     this.productDetails.description = editorResp;
    //     const dbResp = await FirestoreService.addDocumentWithId('products', this.productDetails.id, this.productDetails, Game.converter);
    //     Vue.toasted.show('Product Added Successfully', { icon: 'face', type: 'success' });

    //     this.resetForm();
    //   } catch (error) {
    //     console.log(error)
    //     Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    //   } finally {
    //     this.loading = false;
    //   }
    // } else {
    //   Vue.toasted.show('Please add header image', { icon: 'cross', type: 'error' });
    //   return;
    // }
  }

  async addProductName() {
    try {
      if (!((this.$refs.sellerForm as Vue & { validate: () => boolean }).validate())) {
        return;
      }
      this.NameLading = true;
      this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
      this.productDetails.searchPhrase = this.productDetails.name.toLowerCase();
      console.log(this.productDetails)
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        createdOn: dateNow.getTime()
      }
      this.productDetails.description = this.productDetails.name;
      this.productDetails.auditTrail = auditfield;

      const dbResp = await FirestoreService.addDocumentWithId('products', this.productDetails.id, this.productDetails, Game.converter);
      Vue.toasted.show('Product Added Successfully', { icon: 'face', type: 'success' });
      this.$router.push({ name: 'product', params: { id: this.productDetails.id } });
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });

    } finally {
      this.NameLading = false;
    }

  }
  async getSellers() {
    try {
      this.sellerList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('vendors', SellerInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.sellerList.push(doc.data());
        });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      Vue.toasted.show('Error fetching sellers', { icon: 'face', type: 'error' });

      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  async getPlatforms() {
    try {
      this.platformList = [];
      this.sellerSearchLoading = true;
      const snapshot = await FirestoreService.getDocuments('platforms', PlatformInfo.converter);
      if (!snapshot.empty) {
        snapshot.forEach((doc: any) => {
          this.platformList.push(doc.data());
        });

      } else {
        Vue.toasted.show('No results found', { icon: 'face', type: 'error' });
      }


    } catch (error) {
      Vue.toasted.show('Error fetching sellers', { icon: 'face', type: 'error' });

      console.log(error)

    }
    finally {
      this.sellerSearchLoading = false;
    }

  }
  cancelEdit() {
    this.$store.commit('setCloseSeller', false)
  }
  addSeller() {
    this.sellerEdit = false;
    this.$store.commit('setAddSellerPanel', true);
  }
  addPlatform() {
    this.platformEdit = false;
    this.$store.commit('setAddPlatformPanel', true);
  }
  async updateProduct() {
    if (this.productDetails.headerImageUrl == "") {
      Vue.toasted.show('Please add header image', { icon: 'cross', type: 'error' });
      return;
    }
    try {
      this.loading = true;
      //  this.productDetails.name = this.productDetails.name.trim();
      // this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
      //this.productDetails.searchPhrase = this.productDetails.name.toLowerCase();

      // if (this.selectedImage != null) {
      //   const fileExtension = this.getFileExtension(this.selectedImage);
      //   const resp = await storage.ref('product-images/' + this.productDetails.id + '.' + fileExtension).put(this.selectedImage);
      //   const url = await resp.ref.getDownloadURL();
      //   this.productDetails.headerImageUrl = url;
      //   console.log(this.productDetails.headerImageUrl);

      // }

      // const editorResp = await this.editor.save();
      //  this.productDetails.description = editorResp;
      //  this.productDetails.id = UtilityService.convertToKebabCase(this.productDetails.name) as string;
      const dateNow = new Date();
      const auditfield: AuditFields = {
        createdOn: this.productDetails.auditTrail.createdOn,
        createdBy: this.productDetails.auditTrail.createdBy,
        updatedBy: { uid: auth.currentUser?.uid, name: auth.currentUser?.displayName, email: auth.currentUser?.email },
        updatedOn: dateNow.getTime()
      }
      if (!this.checkSeller(this.productDetails)) {
        Vue.toasted.show('Product should have atleast one seller', { icon: 'cross', type: 'error' });
        return;
      }
      if (!this.checkProducts(this.productDetails)) {
        Vue.toasted.show('Product should have atleast 3 platform objects', { icon: 'cross', type: 'error' });
        return;
      }

      this.productDetails.preOrderDate = this.productDetails.preOrder ? this.preOrderDateFormatted : '';
      this.productDetails.auditTrail = auditfield;
      const dbResp = await FirestoreService.setDocument('products', this.productDetails.id, this.productDetails, Game.converter);
      Vue.toasted.show('Product Updated Successfully', { icon: 'face', type: 'success' });
      this.resetForm();
      await this.getProductDetails();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setCloseSeller', false)
    }

  }
  checkSeller(productDetails: Game) {
    if (productDetails.sellerDetails.length == 0) {
      return false;
    }
    return true;
  }
  checkProducts(productDetails: Game) {
    if (productDetails.platformDetails.length == 0) {
      return false;
    }
    return true;
  }


  getFileExtension(selectedImage: any) {
    return selectedImage.name.substring(selectedImage.name.lastIndexOf(".") + 1);
  }
  editSeller(item: number) {
    this.sellerEdit = true;
    this.editedIndex = item;
    this.$store.commit('setAddSellerPanel', true)
  }


  async deleteSeller(item: number) {
    this.productDetails.sellerDetails.splice(item, 1)
    this.productDetails.sellerDetails = [... this.productDetails.sellerDetails];
  }

  editPlatform(item: number) {
    this.platformEdit = true;
    this.editedIndex = item;
    this.$store.commit('setAddPlatformPanel', true)
  }


  async deletePlatform(item: number) {
    this.productDetails.platformDetails.splice(item, 1)
    this.productDetails.platformDetails = [... this.productDetails.platformDetails];
  }


}
