import { Component, Prop, Vue } from "vue-property-decorator";

import store from '@/store';
import { SellerInfo } from '@/dto/SellerInfo';
import { AuditFields } from '@/dto/AuditFields';
import { FirestoreService } from '@/services/FirestoreService';
import Swal from 'sweetalert2';
import { UtilityService } from '@/services/UtilityService';
import { triggerThreadSafeValidation } from 'vee-validate/dist/types/components/common';
import SellerDetails from "@/dto/SellerDetails";
import { ProductType } from "@/dto/ProductType";

@Component
export default class AddSeller extends Vue {
  @Prop({ required: true })
  sellers!: SellerDetails[];
  @Prop({ required: false, default: 0 })
  selectedIndex!: number;
  @Prop({ required: false, default: false })
  edit!: boolean;
  @Prop({ required: true })
  sellerList!: SellerInfo[];
  seller = new SellerDetails();
  loading = false;
  label = "Add Seller";
  selectedSeller: any = null;

  async mounted() {
    this.label = this.edit ? "Edit Seller" : "Add Seller";
    if (this.edit) {
      this.seller = this.sellers[this.selectedIndex];
      this.selectedSeller = this.seller.sellerInfo;
    } else {
      this.seller = new SellerDetails();
    }
  }
  async validateForm() {
    if ((this.$refs.addSellerForm as Vue & { validate: () => boolean }).validate()) {
      await this.processRequest();
    }
  }
  resetForm() {
    this.seller = new SellerDetails();
    (this.$refs.addSellerForm as Vue & { reset: () => boolean }).reset();
  }

  async processRequest() {
    try {
      if (this.edit)
        await this.updateSeller();
      else {
        await this.addSeller();

      }
    } catch (error) {

    }
  }
  selleChange() {
    this.seller.sellerInfo = this.selectedSeller;

  }
  private checkPromoted() {
    if (this.seller.promoted) {
      this.seller.rank = 0;
      for (const sellerTemp of this.sellers) {
        if (sellerTemp.promoted && sellerTemp.id !== this.seller.id) {
          sellerTemp.promoted = false;
          sellerTemp.rank = sellerTemp.price;
        }
      }
    } else {
      this.seller.rank = this.seller.price;
    }
  }
  findLowestPrice() {
    let lowestIndex = 0;
    let minVal = Number.MAX_VALUE;
    for (const [i, sellerTemp] of this.sellers.entries()) {
      if (sellerTemp.price < minVal) {
        minVal = sellerTemp.price;
        lowestIndex = i;
      }
    }
    for (const [i, sellerTemp] of this.sellers.entries()) {

      sellerTemp.lowestPrice = false;

    }
    this.sellers[lowestIndex].lowestPrice = true;
  }

  async addSeller() {

    try {
      this.checkPromoted();
      this.sellers.push(this.seller);
      this.findLowestPrice();
      this.sellers.sort((a, b) => a.rank - b.rank);
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setAddSellerPanel', false)

    }

  }

  cancelEdit() {
    this.$store.commit('setAddSellerPanel', false)
  }
  async updateSeller() {

    try {
      this.checkPromoted();
      this.sellers[this.selectedIndex] = this.seller;
      this.sellers.sort((a, b) => a.rank - b.rank);
      this.findLowestPrice();
      this.resetForm();
    } catch (error) {
      console.log(error)
      Vue.toasted.show(error.message, { icon: 'cross', type: 'error' });
    } finally {
      this.loading = false;
      this.$store.commit('setAddSellerPanel', false)
    }
  }




}
